}

.event-image-container {
  border-radius: 8px 0px 0px 8px;
  min-width: 200px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 100%;
  border-right: 4px solid #c9920e;
}

.event-details {
  font-size: 13px;
  margin-left: 15px;
}

.event-name {
  font-family: "UnitedSansReg-Heavy", "Graphik-Regular", "Helvetica";
  margin: 0;
  margin-bottom: 7px;
}

.display-date-location-container {
  margin-top: 0px;
}

.display-location {
  margin-left: 15px;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
}

.display-date {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
}

.lucid-banner {
  margin-top: 20px;
  border-radius: 8px;
  background: #c9920e;
}

.lucid-banner .lucid-content {
  text-align: left;
}

#lucid-link {
  background-color: #000;
  color: white;
  border-radius: 8px;
}

.RootBody .ArnSecondarySearchOuterContainer {
  padding: 0px;
}

.RootBody #theOtherSubmitButton {
  font-family: "UnitedSansReg-Heavy", "Graphik-Regular", "Helvetica";
  border-radius: 8px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
  max-width: none;
  min-height: 46px;
  background: #c9920e;
}

input {
  border: 1px solid #c9920e;
  border-radius: 8px;
}

#address-input {
  border: 1px solid #c9920e;
  border-radius: 8px;
}

#theCheckIn,
#theCheckOut,
#rooms,
#adults {
  border: 1px solid #c9920e;
  border-radius: 8px;
}

@media screen and (min-width: 960px) {
  .lucid-banner {
    width: 85vw;
  }

  .lucid-content {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .lucid-content .span {
    font-size: 14px;
  }
}
@media screen and (max-width: 960px) {
  .event-container h2 {
    font-size: 18px;
  }

  .lucid-banner .lucid-content #desktop {
    display: none;
  }

  .lucid-banner .lucid-content #tablet {
    display: none;
  }

  .lucid-banner .lucid-content #mobile {
    display: inline;
  }

  .lucid-banner .lucid-content span {
    margin-left: 15px;
  }
}
@media screen and (max-width: 920px) {
  .RootBody .ArnPrimarySearchContainer {
    flex-direction: column;
    justify-content: none;
    padding-left: 0px;
  }
}
@media screen and (max-width: 786px) {
  .RootBody #theOtherSubmitButton {
    width: 100%;
    margin-top: 20px;
    font-size: 14px;
  }

  .search-container-header-text {
    font-size: 20px;
  }

  .event-image-container {
    min-width: 100px;
    min-height: 200px;
  }

  .event-container h2 {
    font-size: 18px;
    text-align: center;
  }
  .event-container .view-hotels {
    font-size: 14px;
    margin-right: 0px;
  }

  .event-details-and-button {
    flex-direction: column;
  }

  .event-details {
    margin-left: 0px;
  }

  .display-date-location-container {
    margin-bottom: 10px;
    flex-direction: column;
  }

  .display-location {
    font-size: 12px;
  }

  .display-date {
    font-size: 12px;
  }

  .view-hotels {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    padding: 15px;
    border-radius: 8px;
    margin-right: 15px;
    width: unset;
    min-width: 150px;
  }

  .lucid-banner {
    width: 85vw;
  }

  .lucid-banner .lucid-content span {
    margin-left: 15px;
  }
}
@media screen and (max-width: 600px) {
  .lucid-banner .lucid-content #desktop {
    display: none;
  }

  .lucid-banner .lucid-content #tablet {
    display: none;
  }

  .lucid-banner .lucid-content #mobile {
    display: inline;
  }
}